        },
        {
          text: 'L',
          sortable: false,
          length: '5'
        }
      ],
      loading: false
    }
  },
  computed: {
    BS () {
      const lancarData = this.summary.BS['1']
      const macetData = this.summary.BS['0']
      // console.log('lancarData=', lancarData)
      // console.log('macetData=', macetData)
