        ['Kategori 3', macetData['3']],
        ['Kategori 4', macetData['4']],
        ['Kategori 5', macetData['5']]
      ]
      return {
        lancar,
        macet
      }
    },
    BSKeteranganFormatted () {
      return this.BSKeterangan.map((item, idx) => {
        return {
          low: 'Rp, ' + thousand(item.low),
          high: 'Rp, ' + thousand(item.high),
          lancar: this.summary.BS['1'][`${idx + 1}`],
          macet: this.summary.BS['0'][`${idx + 1}`]
        }
      })
    },
    BP () {
      const lancarData = this.summary.BP['1']
      const macetData = this.summary.BP['0']
      // console.log('lancarData=', lancarData)
      // console.log('macetData=', macetData)
      const lancar = [
        ['Kategori 1', lancarData['1']],
        ['Kategori 2', lancarData['2']],
        ['Kategori 3', lancarData['3']],
        ['Kategori 4', lancarData['4']],
        ['Kategori 5', lancarData['5']]
      ]
      const macet = [
        ['Kategori 1', macetData['1']],
        ['Kategori 2', macetData['2']],
        ['Kategori 3', macetData['3']],
        ['Kategori 4', macetData['4']],
        ['Kategori 5', macetData['5']]
      ]
      return {
        lancar,
        macet
      }
    },
    BPKeteranganFormatted () {
      return this.BPKeterangan.map((item, idx) => {
        return {
          low: 'Rp, ' + thousand(item.low),
          high: 'Rp, ' + thousand(item.high),
          lancar: this.summary.BP['1'][`${idx + 1}`],
          macet: this.summary.BP['0'][`${idx + 1}`]
        }
      })
    },
    jkKeteranganFormatted () {
      return [
        {
          kategori: 'Laki - Laki',
          lancar: this.summary.jk['1']['1'],
          macet: this.summary.jk['0']['1']
        },
        {
          kategori: 'Perempuan',
          lancar: this.summary.jk['1']['0'],
          macet: this.summary.jk['0']['0']
        }
      ]
    },
    JBKeteranganFormatted () {
      return [
        {
          kategori: '1%',
          lancar: this.summary.JB['1']['1'],
          macet: this.summary.JB['0']['3']
        },
        {
          kategori: '3%',
          lancar: this.summary.JB['1']['3'],
          macet: this.summary.JB['0']['3']
        }
      ]
    },
    JWP () {
      const lancarData = this.summary.JWP['1']
      const macetData = this.summary.JWP['0']
      // console.log('lancarData=', lancarData)
      // console.log('macetData=', macetData)
      const lancar = [
        ['Pendek', lancarData['1']],
        ['Menengah', lancarData['2']],
        ['Panjang', lancarData['3']]
      ]
      const macet = [
        ['Pendek', macetData['1']],
        ['Menengah', macetData['2']],
        ['Panjang', macetData['3']]
      ]
      return {
        lancar,
        macet
      }
    },
    JWPKeteranganFormatted () {
      return [
        {
          kategori: 'Pendek',
          interval: '1 - 12 bulan',
          macet: this.summary.JWP['0']['1'],
          lancar: this.summary.JWP['1']['1']
        },
        {
          kategori: 'Menengah',
          interval: '13 - 24 bulan',
          macet: this.summary.JWP['0']['2'],
          lancar: this.summary.JWP['1']['2']
        },
        {
          kategori: 'Panjang',
          interval: '25 >',
          macet: this.summary.JWP['0']['3'],
          lancar: this.summary.JWP['1']['3']
        }
      ]
    },
    keteranganSummary () {
      return [
        {
          ket: 'Macet',
          value: this.summary['0']
        },
        {
          ket: 'Lancar',
          value: this.summary['1']
        }
      ]
    }
  },
  methods: {
    async loadSummary (dataset) {
      if (!dataset) {
        return
      }
      this.itemsLoading = true
      try {
        const result = await permintaanAPI.summary(dataset)
        this.summary = result
      } catch (err) {
        console.log(err)
      }
      this.itemsLoading = false
    },
    loadSummaryInDataset () {
      this.loadSummary(this.dataset)
    },
    async loadDatasetSummary () {
      const dsetOptions = constants.optionsDataset
      let datasetSummary = {}

      const summarizeDset = async (dset) => {
        try {
          datasetSummary[dset] = await permintaanAPI.count(dset)
        } catch (err) {
          console.log(`Fail to count dataset = ${dset}`)
          console.log(err)
        }
      }

      for (let i = 0; i < dsetOptions.length; i++) {
        datasetSummary[dsetOptions[i]] = 0
      }

      try {
        await Promise.all(dsetOptions.map(summarizeDset))
        this.datasetSummary = dsetOptions.map(v => {
          return {
            dataset: v,
            jumlah: datasetSummary[v]
          }
        })
      } catch (err) {
        console.log(err)
      }
    },
    async reload () {
      this.loading = true
      await this.loadSummary(this.dataset)
      await this.loadDatasetSummary()
      this.loading = false
    }
  },
  mounted () {
    this.reload()
  }
}
</script>

